// ----------------------------
//  Adjustments to deafult styling
// ----------------------------

.post .post-title {
    text-align: left;
}

.post .post-meta{
    text-align: left;
}

h1 a.black-link {
  color: black !important;
  text-decoration: none !important;
  font-weight: inherit; /* keep heading boldness */
}

h1 a.black-link:hover {
  text-decoration: underline !important;
}


.site-nav .nav-item {
  font-size: 1.2rem; // adjust the number to whatever size you want
}

.home h3 {
  font-weight: normal; // or 400
}


// ----------------------------
//  Home page prof pic
// ----------------------------


img.prof-pic {
  float: left;
  margin-right: 1em;
  width: 25%;
  border-radius: 50%;
}


// ----------------------------
//  Make Pitch Visualizer page wider
// ----------------------------

.page-pitch-visualizer .wrapper,
.page-pitch-editor .wrapper {
  max-width: 1000px;
}


body.wide .wrapper {
  max-width: 1000px; 
}