

// ----------------------------
// Portfolio page post listing with preview
// ----------------------------

.post-preview {
  margin-bottom: 2rem;
}

.preview-container {
  display: flex;
  align-items: flex-start;
  margin-top: 0.5rem;
}


.preview-image {
  width: 300px;
  height: auto;
  max-height: 300px;
  object-fit: cover;
  object-position: top left;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  padding: 4px;
  background-color: white;
  transition: all .5s ease;
  margin-right: 20px;
}

.preview-text {
  flex: 1;
}

.preview-image:hover {
  object-fit: cover;
  width: 400px;
  // height: auto;
  max-height: 800px;

}
