
// ----------------------------
// Slider on homepage
// ----------------------------


.slider-item {
  position: relative;
  width: 300px;
  max-height: 500px;
  overflow: hidden;
  border: 2px solid #ddd;
  border-radius: 8px;
  background-color: #fff;
  margin: 0 10px;
  flex-shrink: 0;
}

.slider-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
  transition: transform 0.5s ease;
  display: block;
  border-radius: 8px;
  z-index: 0;
  position: relative;
}

.slider-item:hover img {
  transform: scale(1.2);
}

/* Overlay and text remain the same */



.slider-item:hover::before {
  content: '';                    /* Add the overlay */
  position: absolute;
  transition: transform 0.5s ease;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.50); /* 50% opaque grey */
  z-index: 1;        
  pointer-events: none;
}

.slider-item .title {
  position: absolute;
  top: 10%; /* Instead of 0 or 50% */
  left: 10%; /* Top-left corner with a little margin */
  transform: none; /* Remove center-translate */
  color: white;
  font-size: 1.3rem;
  font-weight: bold;
  text-align: left;
  z-index: 3;
  opacity: 0;
  transition: opacity 0.5s ease;
  background-color: rgba(0,0,0,0.4); /* optional background for readability */
  padding: 4px 8px;
  border-radius: 4px;
  max-width: 60%;     /* Optional: prevent text from getting too wide */
  word-wrap: break-word;
}
.slider-item:hover .title {
  opacity: 1;                     /* Make the text visible on hover */
}


/* Text styling on hover */
.slider-item .description {
  position: absolute;
  top: 40%;
  left: 10%;
  color: white;                      /* White text */
  font-size: 1.1rem;                 /* Font size */
  font-weight: normal;                 /* Bold text */
  text-align: left;
  z-index: 3;                        /* Ensure text is on top of the overlay */
  opacity: 0;
  transition: opacity 0.5s ease;
  background-color: rgba(0,0,0,0.4); /* optional background for readability */
  padding: 4px 8px;
  max-width: 80%;     /* Optional: prevent text from getting too wide */
  word-wrap: break-word;
  border-radius: 4px;
}

/* Show description text on hover */
.slider-item:hover .description {
  opacity: 1;                      /* Make the text visible on hover */
}
