

// ----------------------------
// Post pages back arrow button
// ----------------------------


/* Button styling */
.arrow-button {
 width: 50px;  /* Set the width of the button */
  height: 50px;  /* Set the height of the button */
  background-image: url('/assets/images/left.png');  /* Replace with the path to your arrow image */
  background-size: contain;  /* Ensure the image fits the button without stretching */
  background-repeat: no-repeat;  /* Prevent the image from repeating */
  background-position: center;  /* Center the image inside the button */
  border: none;  /* Remove default border */
  cursor: pointer;  /* Change cursor to a pointer when hovered */
  outline: none;  /* Remove the outline when focused */
  transition: transform 0.3s ease;  /* Optional: smooth transition effect on hover */
  background-color: transparent;
}

/* Hover effect */
.arrow-button:hover {
  transform: scale(1.2);  /* Slightly increase the button size on hover */
}


// ----------------------------
// Image and video frames/effects in posts
// ----------------------------


.float-left-figure {
  float: left;
  width: 48%;
  margin-right: 50px;
  margin-bottom: 10px;
}

.float-left-figure img {
  width: 100%;
  height: auto;
  border-radius: 5px;
  display: block; /* Ensures no space below image */
}

.float-left-figure figcaption {
  font-style: italic;
  color: #555;
  font-size: 14px;
  margin-top: 5px;
  text-align: left;
}


.float-right-figure {
  float: right;
  width: 38%;
  margin-left: 50px;
  margin-bottom: 10px;
  border: 2px solid #ccc;
  padding: 15px;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
   box-sizing: border-box; 
}

.float-right-figure img,
.float-right-figure video {
  width: 100%;
  height: auto;
  border-radius: 5px;
  display: block; /* Ensures no space below */
}
.float-right-figure img + img {
  margin-top: 10px;
}

.float-right-figure figcaption {
  font-style: italic;
  color: #555;
  font-size: 16px;
  margin-top: 5px;
  text-align: center;
}


.center-figure {
  float: center;
  width: 98%;
  margin-right: 50px;
  margin-bottom: 10px;
  border: 2px solid #ccc;
  padding: 15px;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 20px auto; /* Adds space above and below the figure */
}

.center-figure img {
  width: 100%;
  height: auto;
  border-radius: 5px;
  display: block; /* Ensures no space below image */
}


.center-figure video {
  width: 100%;
  height: auto;
  border-radius: 5px;
  display: block; /* removes inline spacing */
}



.center-figure figcaption {
  font-style: italic;
  color: #555;
  font-size: 14px;
  margin-top: 5px;
  text-align: center;
}

/* Define a reusable class for framed, centered figures with captions */
.framed-figure {
  text-align: center;
  border: 2px solid #ccc;
  padding: 15px;
  background-color: #f9f9f9;
  display: block;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%; /* Adjust width as needed */
  margin: 20px auto; /* Adds space above and below the figure */
}

.framed-figure img {
  max-width: 100%;  /* Ensures image is responsive */
  height: auto;
  border-radius: 5px; /* Optional: adds rounded corners to the image */
}

.framed-figure figcaption {
  font-style: italic;
  margin-top: 10px;
  color: #555;
  font-size: 14px;
}



.video-row {
  display: flex;
  justify-content: center;   /* center them horizontally */
  gap: 20px;                 /* space between videos */
  margin: 20px 0;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.video-row figure {
  width: 48%;                /* each figure takes ~half the row */
  margin: 0;                 /* reset default margins */
  text-align: center;        /* center caption text */
}

.video-row video {
  width: 100%;               /* fill figure width */
  height: auto;
  border-radius: 5px;
  display: block;
}

.video-row figcaption {
  font-style: italic;
  color: #555;
  font-size: 14px;
  margin-top: 5px;
}


.hover-zoom {
  transition: transform 0.3s ease;
  cursor: pointer;
}
.hover-zoom:hover {
  transform: scale(2);
  z-index: 2;
}


.hover-zoom_small {
  transition: transform 0.3s ease;
  cursor: pointer;
}
.hover-zoom_small:hover {
  transform: scale(1.2);
  z-index: 2;
}

